import React, {useState} from 'react';

function Icon(props){
    const [path] = useState(props.path);
    const [className] = useState(props.className ? props.className : null);
    return (
    <img
        onClick = {props.onClick ? props.onClick : null}
        className = {className}
        title = {props.title ? props.title : null}
        src = {path} alt = 'icon'>
    </img>
    )
}

export default Icon;